<template>
  <!-- 頂部控制區 -->
  <v-row>
    <v-col cols="12" md="4">
      <v-select v-model="selectedRole" :items="roles" item-title="name" item-value="id" label="選擇角色" variant="outlined"
        density="compact" @update:model-value="loadRolePermissions"></v-select>
    </v-col>
    <v-col cols="12" md="8" class="d-flex justify-end">
      <v-btn color="primary" prepend-icon="mdi-download" @click="downloadPermissions">
        下載權限清查表
      </v-btn>
    </v-col>
  </v-row>

  <!-- 權限設定區 -->
  <v-row>
    <v-col cols="12">
      <v-card class="permission-card">
        <v-card-title class="d-flex justify-space-between align-center">
          <span>儀表板</span>
          <div class="d-flex align-center">
            <v-text-field v-model="searchText" prepend-inner-icon="mdi-magnify" placeholder="搜尋" variant="outlined"
              density="compact" width="300" hide-details clearable class="me-2"></v-text-field>
            <v-btn color="primary" @click="searchPermissions" text="尋找"></v-btn>
          </div>
        </v-card-title>

        <v-card-text>
          <div class="permissions-tree">
            <!-- 動態生成權限分類 -->
            <template v-for="category in permissionCategories" :key="category.id">
              <div v-if="permissions[category.id]" class="permission-category">
                <v-checkbox
                  v-model="permissions[category.id].selected"
                  :indeterminate="permissions[category.id].indeterminate"
                  hide-details
                  :label="`${category.name} (${category.nameEn})`"
                  color="teal"
                  class="category-checkbox"
                  @change="toggleCategory(category.id)"
                ></v-checkbox>

                <div class="ms-8" v-show="permissions[category.id].expanded">
                  <v-checkbox
                    v-for="item in category.items"
                    :key="item.id"
                    v-model="permissions[category.id].items[item.id]"
                    :label="item.name"
                    hide-details
                    color="teal"
                    class="subcategory-checkbox"
                    @change="updateCategoryState(category.id)"
                  ></v-checkbox>
                </div>
              </div>
            </template>
          </div>
        </v-card-text>

        <v-card-actions class="pa-4">
          <v-spacer></v-spacer>
          <v-btn color="grey" variant="outlined" @click="resetPermissions">
            重置
          </v-btn>
          <v-btn color="primary" @click="savePermissions" :loading="saving">
            儲存設定
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>

  <!-- 載入中對話框 -->
  <v-dialog v-model="loading" persistent width="300">
    <v-card>
      <v-card-text class="text-center pa-6">
        <v-progress-circular indeterminate color="primary" size="64" class="mb-4"></v-progress-circular>
        <div>載入中...</div>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, reactive, onMounted, computed, watch } from "vue";
import { useRouter } from "vue-router";

const router = useRouter();

// 狀態管理
const loading = ref(false);
const saving = ref(false);
const selectedRole = ref("");
const searchText = ref("");

// 角色列表
const roles = ref([
  { id: "admin", name: "系統管理員" },
  { id: "coach", name: "教練" },
  { id: "analyst", name: "數據分析師" },
  { id: "doctor", name: "醫療人員" },
  { id: "athlete", name: "選手" },
]);

// 權限分類定義
const permissionCategories = ref([
  {
    id: "physiological",
    name: "生理監控",
    nameEn: "Physiological",
    items: [
      { id: "template1", name: "選手生理監測儀表板" },
      { id: "template2", name: "生理狀態綜合儀表板" },
      { id: "template3", name: "運動生理數據總覽" },
    ],
  },
  {
    id: "psychological",
    name: "心理監控",
    nameEn: "Psychological",
    items: [
      { id: "template1", name: "心理健康與壓力監控儀表板" },
      { id: "template2", name: "選手心理狀態追蹤儀表板" },
      { id: "template3", name: "心理與恢復趨勢總覽" },
    ],
  },
  {
    id: "nutrition",
    name: "營養監控",
    nameEn: "Nutrition / Hydration",
    items: [
      { id: "template1", name: "水分補給與代謝監控儀表板" },
      { id: "template2", name: "營養補給行為分析儀表板" },
      { id: "template3", name: "飲水與汗液電解平衡儀表板" },
    ],
  },
  {
    id: "performance",
    name: "體能監控",
    nameEn: "Performance",
    items: [
      { id: "template1", name: "體能表現與爆發力分析儀表板" },
      { id: "template2", name: "跳躍能力與肌肉性分析儀表板" },
    ],
  },
]);

// 權限狀態結構 - 立即初始化
const permissions = reactive({});

// 初始化權限結構
const initializePermissions = () => {
  permissionCategories.value.forEach((category) => {
    permissions[category.id] = {
      selected: false,
      indeterminate: false,
      expanded: true,
      items: {},
    };

    // 初始化子項目
    category.items.forEach((item) => {
      permissions[category.id].items[item.id] = false;
    });
  });
};

// 立即初始化權限結構
initializePermissions();

// 計算屬性
const hasSelectedRole = computed(() => !!selectedRole.value);

// 方法
const loadRolePermissions = async (roleId) => {
  if (!roleId) return;

  loading.value = true;
  try {
    // 模擬 API 調用
    await new Promise((resolve) => setTimeout(resolve, 1000));

    // 根據角色載入相應的權限設定
    loadPermissionsByRole(roleId);
  } catch (error) {
    console.error("載入權限失敗:", error);
  } finally {
    loading.value = false;
  }
};

const loadPermissionsByRole = (roleId) => {
  // 重置所有權限
  resetPermissions();

  // 根據角色類型設定預設權限
  switch (roleId) {
    case "admin":
      // 系統管理員擁有所有權限
      setAllPermissions(true);
      break;
    case "coach":
      // 教練可以查看所有監控數據
      permissions.physiological.items.template1 = true;
      permissions.psychological.items.template1 = true;
      permissions.nutrition.items.template1 = true;
      permissions.performance.items.template1 = true;
      break;
    case "analyst":
      // 數據分析師主要看綜合分析
      permissions.physiological.items.template2 = true;
      permissions.psychological.items.template3 = true;
      permissions.nutrition.items.template2 = true;
      permissions.performance.items.template2 = true;
      break;
    case "doctor":
      // 醫療人員主要看生理和心理數據
      permissions.physiological.items.template1 = true;
      permissions.physiological.items.template2 = true;
      permissions.psychological.items.template1 = true;
      break;
    case "athlete":
      // 選手只能查看自己的基本數據
      permissions.physiological.items.template1 = true;
      permissions.psychological.items.template2 = true;
      break;
  }

  // 更新分類狀態
  updateAllCategoryStates();
};

const setAllPermissions = (value) => {
  permissionCategories.value.forEach((category) => {
    category.items.forEach((item) => {
      permissions[category.id].items[item.id] = value;
    });
    permissions[category.id].selected = value;
    permissions[category.id].indeterminate = false;
  });
};

const toggleCategory = (categoryKey) => {
  const category = permissions[categoryKey];
  const newValue = category.selected; // 直接用 selected 狀態

  // 設定該分類下所有子項目
  Object.keys(category.items).forEach((item) => {
    category.items[item] = newValue;
  });

  // 更新分類狀態
  updateCategoryState(categoryKey);
};

const updateCategoryState = (categoryKey) => {
  const category = permissions[categoryKey];
  const items = Object.values(category.items);
  const checkedCount = items.filter((item) => item).length;
  const totalCount = items.length;

  if (checkedCount === 0) {
    category.selected = false;
    category.indeterminate = false;
  } else if (checkedCount === totalCount) {
    category.selected = true;
    category.indeterminate = false;
  } else {
    category.selected = false;
    category.indeterminate = true;
  }
};

const updateAllCategoryStates = () => {
  permissionCategories.value.forEach((category) => {
    updateCategoryState(category.id);
  });
};

const resetPermissions = () => {
  permissionCategories.value.forEach((category) => {
    permissions[category.id].selected = false;
    permissions[category.id].indeterminate = false;
    category.items.forEach((item) => {
      permissions[category.id].items[item.id] = false;
    });
  });
};

const savePermissions = async () => {
  if (!selectedRole.value) {
    alert("請先選擇角色");
    return;
  }

  saving.value = true;
  try {
    // 收集選中的權限
    const selectedPermissions = {};
    permissionCategories.value.forEach((category) => {
      selectedPermissions[category.id] = [];
      category.items.forEach((item) => {
        if (permissions[category.id].items[item.id]) {
          selectedPermissions[category.id].push(item.id);
        }
      });
    });

    // 模擬 API 調用
    await new Promise((resolve) => setTimeout(resolve, 1500));

    console.log("儲存權限設定:", {
      roleId: selectedRole.value,
      permissions: selectedPermissions,
    });

    alert("權限設定已儲存");
  } catch (error) {
    console.error("儲存權限失敗:", error);
    alert("儲存失敗，請重試");
  } finally {
    saving.value = false;
  }
};

const downloadPermissions = async () => {
  if (!selectedRole.value) {
    alert("請先選擇角色");
    return;
  }

  try {
    // 模擬下載權限清查表
    const blob = new Blob(["權限清查表內容"], { type: "application/zip" });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.download = `權限清查表_${selectedRole.value
      }_${new Date().getTime()}.zip`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  } catch (error) {
    console.error("下載失敗:", error);
    alert("下載失敗，請重試");
  }
};

const searchPermissions = () => {
  if (!searchText.value.trim()) return;

  console.log("搜尋權限:", searchText.value);
  // 實現搜尋邏輯
};

// 監聽搜尋文字變化
watch(searchText, (newValue) => {
  if (!newValue) {
    // 清空搜尋時恢復所有項目顯示
    permissionCategories.value.forEach((category) => {
      permissions[category.id].expanded = true;
    });
  }
});

// 初始化
onMounted(() => {
  // 預設選擇第一個角色
  if (roles.value.length > 0) {
    selectedRole.value = roles.value[0].id;
    loadRolePermissions(selectedRole.value);
  }
});
</script>

<style scoped>
.permission-card {
  background-color: #f0f9ff;
  min-height: 500px;
}

.permissions-tree {
  padding: 1rem 0;
}

.permission-category {
  margin-bottom: 1.5rem;
  border-left: 3px solid #26a69a;
  padding-left: 1rem;
}

.category-checkbox :deep(.v-label) {
  font-weight: 600;
  font-size: 1.1rem;
  color: #26a69a;
}

.subcategory-checkbox :deep(.v-label) {
  font-size: 0.95rem;
  color: #555;
}

.subcategory-checkbox {
  margin-bottom: 0.25rem;
}

:deep(.v-checkbox .v-selection-control__input) {
  color: #26a69a;
}

:deep(.v-checkbox .v-selection-control__input .v-icon) {
  color: #26a69a;
}

.permission-card :deep(.v-card-title) {
  background-color: rgba(38, 166, 154, 0.1);
  border-bottom: 1px solid #e0e0e0;
}
</style>
